body {
  box-sizing: border-box;
  font-family: Arial;
}

.people {
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  align-items: center;
  gap: 40px 30px;
  padding: 30px 35px 0;
  display: grid;
}

.people__person {
  justify-items: center;
  width: 200px;
  height: 231px;
  padding: 15px;
  display: block;
}

.people__person-photo {
  justify-content: center;
  width: 200px;
  height: 200px;
  padding-bottom: 8px;
  position: relative;
}

.people__person-photo img {
  object-fit: cover;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  transition: all 1s;
  display: block;
}

.people__person-photo-rank {
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  line-height: 100%;
  display: flex;
  position: absolute;
  bottom: 84px;
  right: 10px;
}

.people__person-details {
  justify-items: center;
  display: block;
}

.people__person-details-name {
  text-align: center;
  color: #2f4f4f;
  font-size: 20px;
  font-weight: 700;
  line-height: 100%;
}

.people__person-details-extra {
  text-align: center;
  color: #000;
  padding-top: 9px;
  font-family: Arial;
  font-size: 18px;
  font-weight: 400;
  line-height: 100%;
  display: none;
}

.people__person:hover {
  z-index: 1;
  background-color: #eee;
  border-radius: 10px;
  scale: 1.2;
}

.people__person:hover .people__person-photo img {
  border-radius: 30%;
  transition: all 1s;
}

.people__person:hover .people__person-details-extra {
  display: block;
}

@media (width >= 751px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}
